@font-face {
  font-family: trebuc;
  font-style: normal;
  font-weight: 100;
  src: url("./assets/fonts/trebuc.ttf");
  /* src: url("./fonts/trebuc.eot?#iefix") format("embedded-opentype"),
     url("./fonts/trebuc.woff2") format("woff2"),
     url("./fonts/trebuc.woff")
      format("woff"),
     url("./fonts/trebuc.ttf")
      format("truetype"); */
}
* {
  list-style-type: none;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: trebuc;
  text-decoration: none;
  border: none;
  outline: none;
}
a {
  cursor: pointer;
}

.App {
  background-color: #ffd200;
}

/* section 1 */
.header-box {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 4;
  transition: all 0.4s linear;
  background-color: #ffd200;
}
.header-box-change {
  background-color: white;
  transition: all 0.4s linear;
}
.header {
  height: 80px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: transparent;
}

.container {
  max-width: 91.666667%;
  margin: 0 auto;
}
.logo {
  margin-left: 50px;
}
.logo img {
  width: 150px;
}
.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 900;
}
.menu-item {
  padding: 6px 12px;
  margin: 0px 5px;
  cursor: pointer;
}
.menu-item:hover {
  border-radius: 30px;
  background-color: #000;
  color: #fff;
  padding: 6px 12px;
  transition: all 0.2s linear;
}
.menu-item-icon {
  border-radius: 100%;
  cursor: pointer;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.menu-item-icon:hover {
  background-color: rgba(0, 0, 0, 0.04);
  transition: 0.2s linear;
}
.menu-icon {
  font-size: 28px !important;
  color: #000;
  transition: all 0.4s linear;
}
.menu-icon-change {
  color: #026764;
  transition: all 0.4s linear;
}
.menu-login,
.menu-signup {
  cursor: pointer;
  border-width: 1px;
  border-radius: 30px;
  font-size: 12px;
  border-style: solid;
  padding: 8px 10px;
  margin: 0px 5px;
  transition: all 0.4s linear;
  color: #000;
  background-color: transparent;
  border: 1px solid #000;
}
.menu-signup {
  background-color: #000;
  color: #ffd200;
}
.menu-login-change {
  color: #fff;
  background-color: #026764;
  border: 1px solid #026764;
  transition: all 0.4s linear;
}
.menu-signup-change {
  color: #026764;
  background-color: transparent;
  border: 1px solid #026764;
  transition: all 0.4s linear;
}

/* section 2 */
.banner {
  background-color: #ffd200;
  position: fixed;
  min-height: 480px;
  width: 100%;
  top: 80px;
}
.banner-image-box {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
.banner-image-box img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.banner-title {
  padding: 4px 0;
  font-size: 30px;
}
.banner-subtitle {
  padding: 4px 0;
  font-size: 18px;
}
.happy-btn {
  padding: 4px 8px;
  border-radius: 30px;
  background-color: #000;
  cursor: pointer;
  color: #fff;
  font-size: 18px;
  height: 50px;
  padding: 12px;
  transition: all 0.4s linear;
}
.happy-btn:hover {
  opacity: 0.8;
}
.happy {
  border-radius: 8px;
  background-color: #026764;
  padding: 4px 5px;
}

.fix-text-box {
  position: relative;
}
.fix-text {
  min-height: 440px;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 30px;
}

/* section 3 */
.body {
  margin-top: 560px;
  position: relative;
  background-color: #fff;
}
.search-box {
  padding: 10px 0;
  position: -webkit-sticky;
  position: sticky;
  top: 80px;
  background-color: #fff;
  z-index: 3;
  box-shadow: 1px 0 10px -5px #a5a5a5;
  height: 70px;
}
.search {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
  font-weight: 900;
}
.search-left {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.asap-box {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.asap-title {
  color: #f79f1f;
  text-align: center;
}
.asap-text {
  color: #a5a5a5;
}
.delivery-pickup {
  margin-left: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 18px;
}
.change {
  padding: 8px 10px;
  border-radius: 30px;
  background-color: #000;
  color: #fff;
  font-size: 12px;
}
.receive-box {
  display: flex;
  margin-left: 8px;
  align-items: center;
  justify-content: center;
}
.receive {
  border-bottom: 1px solid transparent;
  margin: 0px 4px;
  position: relative;
}

.receive-active:after {
  display: block;
  background-color: #026764;
  height: 2px;
  content: "";
  position: absolute;
  width: 100%;
  bottom: -4px;
}

/* carousel  */
.slick-next {
  right: -9px !important;
  z-index: 2;
  /* transform: scale(0) !important; */
}
.slick-prev {
  left: -12px !important;
  z-index: 2;
  /* transform: scale(0) !important; */
}
.carousel-icon {
  color: #fff;
  background-color: #286460;
  width: 50px !important;
  height: 50px !important;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  font-size: 24px !important;
}
.slick-next,
.slick-prev {
  width: 50px !important;
  height: 50px;
  transform: unset;
}
.slick-prev:before,
.slick-next:before {
  display: none;
}
.carousel-box {
  transform: translateX(-5px);
  display: flex;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translateX(-5px);
  margin: 0 !important;
}
.carousel-title {
  margin-top: 30px;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 700;
  padding-bottom: 10px;
}
.slick-disabled {
  display: none !important;
}

.arrow-box-prev,
.arrow-box-next {
  transform: scale(0);
}
.slick-slider.slick-initialized:hover .arrow-box-prev,
.slick-slider.slick-initialized:hover .arrow-box-next {
  transform: scale(1);
  transition: transform 0.4s cubic-bezier(0.855, -0.6, 0.215, 1.65);
}
@keyframes bounce {
  0% {
    transform: scale(1);
  }
  70% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
.parent {
  height: 1px;
  background-color: #d7d7d7;
  margin-bottom: 20px;
  position: relative;
}
.child {
  width: 30px;
  height: 2px;
  background-color: #000;
  transition: all 0.2s linear;
}

/* CARD */
.card {
  padding: 0 5px;
  -webkit-user-drag: none;
  -moz-window-dragging: none;
}
.card-image-box {
  position: relative;
  overflow: hidden;
  width: 100%;
  border-radius: 5px;
  height: 220px;
  margin-bottom: 10px;
}
.card-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card-body {
  padding: 5px 8px;
}
.card-title {
  font-size: 18px;
}
.card-cat {
  color: #026764;
  font-size: 15px;
}
.card-desc-text {
  color: #696969;
  font-size: 15px;
}
.card-label {
  position: absolute;
  font-size: 15px;
  padding: 4px 8px;
  border-radius: 20px;
  cursor: pointer;
  bottom: 15px;
  right: 15px;
  background-color: #000;
  color: #f5f5f5;
  opacity: 0.85;
}
.card-timer-box {
  display: flex;
  align-items: center;
  -webkit-transition: background-color 0.5s, opacity 0.8s;
  transition: background-color 0.5s, opacity 0.8s;
  color: #000;
  justify-content: center;
  position: sticky;
}
.card-timer {
  margin-top: 16px;
  background-color: #fff;
  padding: 4px 8px;
  border-radius: 30px;
  font-size: 15px;
  display: flex;
  align-items: center;
}
.card-timer-icon {
  font-size: 15px !important;
  margin-right: 4px;
}
.card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card-time-price {
  display: flex;
  align-items: center;
  font-weight: 900;
}
.card-time-icon-box {
  background-color: #f5f5f5;
  font-size: 12px;
  margin: 1px;
  padding: 2px;
  display: flex;
  align-items: center;
}
.card-time-icon {
  font-size: 14px !important;
}
.card-time {
  margin-left: 4px;
  font-weight: 900;
}
.card-price {
  background-color: #d7d7d7;
  font-size: 12px;
  margin: 1px;
  padding: 2px;
}
.card-delivery {
  color: #026764;
  font-size: 12px;
  background-color: #f5f5f5;
  margin: 1px;
  padding: 2px;
}

/* ready  */
.ready {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.ready-box {
  border-top: 0.5px solid #d7d7d7;
  padding: 30px 0px 60px;
  margin-top: 30px;
}
.ready-top {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.ready-bottom {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: row;
  margin-top: 30px;
}
.ready-title {
  font-size: 25px;
}
.ready-desc {
  font-size: 18px;
  margin-top: 10px;
}

/* footer */

.footer-box {
  padding-top: 30px;
  background-color: #fff;
  z-index: 4;
  position: relative;
}
.footer-parent {
  background-color: #000;
  width: 100%;
}
.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 75%;
  margin: 0 auto;
  padding: 0 0 50px;
}
.footer-img-box {
  max-width: 66.666667%;
  height: 60px;
  top: -29px;
  margin: 0 auto;
  position: relative;
}
.footer-img {
  width: 100%;
  height: 100%;
}
.footer-links {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  border-bottom: 0.5px solid #464646;
  margin: 0 0 20px;
}
.footer-link {
  padding: 20px 10px;
  border-bottom: 2px solid transparent;
  -webkit-transition: 0.5s linear;
  transition: 0.5s linear;
  color: #a5a5a5;
}
.footer-link:hover {
  border-color: #04cbc5;
  color: #04cbc5;
}
.footer-links-group-box {
  display: flex;
  justify-content: space-between;
  width: 100%;
  flex-direction: column;
}
.footer-links-left {
  display: inline-flex;
  flex-grow: 1;
  width: unset;
  flex-wrap: wrap;
}

.footer-links-left-boxes {
  padding-right: 20px;
  margin: 20px 0;
  width: 25%;
}

.footer-links-left-header,
.footer-links-right-header {
  color: #fff;
  font-size: 20px;
  font-weight: 700;
}
.footer-links-left-body {
  display: flex;
  flex-direction: column;
}

.footer-link2 {
  color: #a5a5a5;
  padding: 4px 0;
  transition: 0.4s linear;
  font-weight: 900;
}
.footer-link2:hover {
  color: #04cbc5;
}
.footer-links-right {
  width: 100%;
  padding-right: 20px;
  margin: 20px 0;
}
.footer-links-right-body {
  display: flex;
  flex-direction: column;
}
.footer-bottom {
  padding: 30px 0 0;
  border-top: 0.5px solid #282828;
  width: 100%;
}
.footer-bottom-description {
  margin: 20px 0 30px;
  font-size: 16px;
}
.footer-bottom-title {
  color: #d7d7d7;
  margin-bottom: 10px;
}
.footer-bottom-text {
  color: #a5a5a5;
  text-align: justify;
}
.footer-social-terms {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer-social-terms-links {
  color: #a5a5a5;
}
.copy-right {
  padding-right: 10px;
}
.terms {
  padding: 0 10px;
  display: inline-block;
}
.social {
  padding: 12px;
}
.social-box {
  padding: 12px;
  color: rgba(0, 0, 0, 0.54);
}
.social-icon {
  color: #fff;
  font-size: 20px;
}
.social-icon:hover {
  color: #04cbc5;
  transition: 0.2s linear;
}

@media (max-width: 960px) {
  .header {
    flex-direction: column;
    height: 120px;
    justify-content: center;
  }
  .logo {
    margin: 0;
  }

  .fix-text {
    justify-content: flex-start;
  }
  .happy-btn {
    margin-top: 10px;
  }
  .footer {
    max-width: 91.666667%;
  }
  .footer-links {
    justify-content: flex-start;
  }
  .footer-links-left-boxes {
    width: 50%;
  }
}
@media (max-width: 600px) {
  .header {
    flex-direction: row;
    height: 80px;
    justify-content: space-between;
  }
  .menu-item {
    display: none;
  }
  .logo img {
    width: 80px;
  }
  .banner-title {
    font-size: 20px;
  }
  .search-box {
    height: 120px;
  }
  .search-left {
    flex-direction: column;
  }
  .ready-bottom {
    flex-direction: column;
  }
  .delivery-pickup {
    margin-top: 10px;
  }
  .footer-links-left-boxes {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .footer-links {
    flex-wrap: wrap;
  }
  .footer-links-left-body {
    text-align: center;
  }
  .footer-links-right {
    text-align: center;
  }
  .footer-social-terms {
    flex-wrap: wrap;
  }
  .social {
    padding-left: 0;
  }
  .footer-img-box {
    max-width: 100%;
  }
}
